canvas {
  height: 520px;
}

.spacing-element-for-canvas {
  height: 480px;
  z-index: -1;
  position: 'relative';
}

.main-content {
  /* position: relative; */
  /* z-index: 0; */
}

.nav-arrow {
  position: relative;
  z-index: 2;
  margin: 0 auto;
  width: 25px;
  height: 50px;
  cursor: pointer;
}

.nav-arrow-left-tip, .nav-arrow-right-tip {
  position: absolute;
  z-index: 2;
  width: 1px;
  height: 15px;
  bottom: 0;
  background-color: black;
}
.nav-arrow-left-tip {
  position: absolute;
  transform: rotate(-55deg);
  left: 6px;
}
.nav-arrow-right-tip {
  position: absolute;
  transform: rotate(55deg);
  right: 6px;
}

.sphereHtml {
  background-color: transparent;
  cursor: crosshair;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}